'use strict';

// MODULES //

var Readable = require( 'readable-stream' ).Readable;
import isCollection from '@stdlib/assert-is-collection';
import isError from '@stdlib/assert-is-error';
import isBuffer from '@stdlib/assert-is-buffer';
import format from '@stdlib/string-format';
import assign from '@stdlib/object-assign';
import inherit from '@stdlib/utils-inherit';
import setNonEnumerable from '@stdlib/utils-define-nonenumerable-property';
import setNonEnumerableReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import string2buffer from '@stdlib/buffer-from-string';
import Buffer from '@stdlib/buffer-ctor'; // TODO: replace Buffer.concat usage with stdlib pkg
import nextTick from '@stdlib/utils-next-tick';
import DEFAULTS from './defaults.json';
import validate from './validate.js';
import debug from './debug.js';


// FUNCTIONS //

/**
* Implements the `_read` method.
*
* @private
* @param {number} size - number (of bytes) to read
* @returns {void}
*/
function read() {
	/* eslint-disable no-invalid-this */
	var FLG;
	var err;
	var v;

	if ( this._destroyed ) {
		return;
	}
	FLG = true;
	while ( FLG ) {
		err = null;

		this._i += 1;
		if ( this._i > this._src.length ) {
			debug( 'Finished iteration.' );
			return this.push( null );
		}
		v = this._src[ this._idx ];
		debug( 'Value: %s. Idx: %d. Iter: %d.', JSON.stringify( v ), this._idx, this._i );

		this._idx += this._stride;
		if ( this._objectMode === false ) {
			v = this._serialize( v );
			if ( typeof v === 'string' ) {
				if ( this._i === 1 ) {
					v = string2buffer( v );
				} else {
					v = string2buffer( this._sep+v );
				}
			} else if ( isBuffer( v ) ) {
				if ( this._i > 1 ) {
					v = Buffer.concat( [ string2buffer( this._sep ), v ] );
				}
			} else {
				err = new Error( format( 'invalid operation. Serialization function must return a string or Buffer. Value: `%s`.', v ) );
			}
		}
		if ( err ) {
			this.emit( 'error', err );
		} else {
			FLG = this.push( v );
		}
	}

	/* eslint-enable no-invalid-this */
}

/**
* Gracefully destroys a stream, providing backward compatibility.
*
* @private
* @param {(string|Object|Error)} [error] - error
* @returns {ArrayStream} Stream instance
*/
function destroy( error ) {
	/* eslint-disable no-invalid-this */
	var self;
	if ( this._destroyed ) {
		debug( 'Attempted to destroy an already destroyed stream.' );
		return this;
	}
	self = this;
	this._destroyed = true;

	nextTick( close );

	return this;

	/**
	* Closes a stream.
	*
	* @private
	*/
	function close() {
		if ( error ) {
			debug( 'Stream was destroyed due to an error. Error: %s.', ( isError( error ) ) ? error.message : JSON.stringify( error ) );
			self.emit( 'error', error );
		}
		debug( 'Closing the stream...' );
		self.emit( 'close' );
	}

	/* eslint-enable no-invalid-this */
}


// MAIN //

/**
* Stream constructor for generating a readable stream from an array-like object.
*
* @constructor
* @param {Collection} src - source array-like object
* @param {Options} [options] - stream options
* @param {boolean} [options.objectMode=false] - specifies whether the stream should operate in object mode
* @param {(string|null)} [options.encoding=null] - specifies how `Buffer` objects should be decoded to strings
* @param {NonNegativeNumber} [options.highWaterMark] - specifies the maximum number of bytes to store in an internal buffer before pausing streaming
* @param {string} [options.sep='\n'] - separator used to join streamed data
* @param {Function} [options.serialize] - custom serialization function
* @param {integer} [options.dir=1] - iteration direction
* @throws {TypeError} first argument must be an array-like object
* @throws {TypeError} options argument must be an object
* @throws {TypeError} must provide valid options
* @returns {ArrayStream} Stream instance
*
* @example
* import inspectStream from '@stdlib/streams-node-inspect-sink';
* import Float64Array from '@stdlib/array-float64';
* import randu from '@stdlib/random-base-randu';
*
* function log( chunk ) {
*    console.log( chunk.toString() );
* }
*
* var arr = new Float64Array( 10 );
* var i;
* for ( i = 0; i < arr.length; i++ ) {
*     arr[ i ] = randu();
* }
*
* var stream = new ArrayStream( arr );
*
* stream.pipe( inspectStream( log )  );
*/
function ArrayStream( src, options ) {
	var opts;
	var err;
	if ( !( this instanceof ArrayStream ) ) {
		if ( arguments.length > 1 ) {
			return new ArrayStream( src, options );
		}
		return new ArrayStream( src );
	}
	if ( !isCollection( src ) ) {
		throw new TypeError( format( 'invalid argument. First argument must be an array-like object. Value: `%s`.', src ) );
	}
	opts = assign( {}, DEFAULTS );
	if ( arguments.length > 1 ) {
		err = validate( opts, options );
		if ( err ) {
			throw err;
		}
	}
	// Make the stream a readable stream:
	debug( 'Creating a readable stream configured with the following options: %s.', JSON.stringify( opts ) );
	Readable.call( this, opts );

	// Destruction state:
	setNonEnumerable( this, '_destroyed', false );

	// Cache whether the stream is operating in object mode:
	setNonEnumerableReadOnly( this, '_objectMode', opts.objectMode );

	// Cache the separator:
	setNonEnumerableReadOnly( this, '_sep', opts.sep );

	// Define the serialization function:
	setNonEnumerableReadOnly( this, '_serialize', opts.serialize || JSON.stringify );

	// Cache the data source:
	setNonEnumerableReadOnly( this, '_src', src );

	// Cache the array "stride":
	setNonEnumerableReadOnly( this, '_stride', opts.dir );

	// Initialize an iteration counter:
	setNonEnumerable( this, '_i', 0 );

	// Initialize the source index (pointer):
	setNonEnumerable( this, '_idx', ( opts.dir === 1 ) ? 0 : src.length-1 );

	return this;
}

/*
* Inherit from the `Readable` prototype.
*/
inherit( ArrayStream, Readable );

/**
* Implements the `_read` method.
*
* @private
* @name _read
* @memberof ArrayStream.prototype
* @type {Function}
* @param {number} size - number (of bytes) to read
* @returns {void}
*/
setNonEnumerableReadOnly( ArrayStream.prototype, '_read', read );

/**
* Gracefully destroys a stream, providing backward compatibility.
*
* @name destroy
* @memberof ArrayStream.prototype
* @type {Function}
* @param {(string|Object|Error)} [error] - error
* @returns {ArrayStream} Stream instance
*/
setNonEnumerableReadOnly( ArrayStream.prototype, 'destroy', destroy );


// EXPORTS //

export default ArrayStream;
