'use strict';

/**
* Create a readable stream from an array-like object.
*
* @module @stdlib/streams-node-from-array
*
* @example
* import inspectStream from '@stdlib/streams-node-inspect-sink';
* import Float64Array from '@stdlib/array-float64';
* import randu from '@stdlib/random-base-randu';
* import arrayStream from '@stdlib/streams-node-from-array';
*
* function log( chunk ) {
*    console.log( chunk.toString() );
* }
*
* var arr = new Float64Array( 10 );
* var i;
* for ( i = 0; i < arr.length; i++ ) {
*     arr[ i ] = randu();
* }
*
* var stream = arrayStream( arr );
*
* stream.pipe( inspectStream( log ) );
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import randu from '@stdlib/random-base-randu';
* import arrayStream from '@stdlib/streams-node-from-array';
*
* var arr = new Float64Array( 10 );
* var i;
* for ( i = 0; i < arr.length; i++ ) {
*     arr[ i ] = randu();
* }
*
* var opts = {
*     'sep': ',',
*     'objectMode': false,
*     'encoding': 'utf8',
*     'highWaterMark': 64
* };
*
* var createStream = arrayStream.factory( opts );
*
* // Create 10 identically configured streams...
* var streams = [];
* for ( i = 0; i < 10; i++ ) {
*     streams.push( createStream( arr ) );
* }
*
* @example
* import inspectStream from '@stdlib/streams-node-inspect-sink';
* import Float64Array from '@stdlib/array-float64';
* import randu from '@stdlib/random-base-randu';
* import arrayStream from '@stdlib/streams-node-from-array';
*
* function log( v ) {
*    console.log( v );
* }
*
* var arr = new Float64Array( 10 );
* var i;
* for ( i = 0; i < arr.length; i++ ) {
*     arr[ i ] = randu();
* }
*
* var stream = arrayStream.objectMode( arr );
*
* stream.pipe( inspectStream.objectMode( log ) );
*/


// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import objectMode from './object_mode.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'objectMode', objectMode );
setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
